.avis {
  width: 100%;

  &__frame {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  &__button {
    background: $color-gold;
    padding: 10px;
    border: none;
    border-radius: 10px;

    &:hover {
      box-shadow: 0px 2px 2px $color_light;
      transform: scale(1.1);
      transition: all 0.2s ease-in-out;
    }

    &__link {
      color: $color-dark;
      text-decoration: none;
      font-size: $fontSize_normal;
    }
  }
}

@media only screen and (min-width: $sm) and (max-width: $xl) {
  .avis {
    width: 70%;
    margin: auto;
  }
}

@media only screen and (min-width: $xl) {
  .avis {
    width: 70%;
    margin: auto;
  }
}
